@media (max-width: 600px) {
    .text-margin {
        margin-left: auto;
        margin-right: auto;
    }

    .block {
        padding: 30px;
        background-color: rgba(0, 0, 0, 0.4);
        border-radius: 6px;
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.15), 0 1px 0 rgba(255, 255, 255, 0.04);
        -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .15), 0 1px 0 rgba(255, 255, 255, .04);
        -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.15), 0 1px 0 rgba(255, 255, 255, 0.04);
        width: 100px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 20px;
    }

    .block-container{
        width: 90%;
    }

    .button {
        margin-left: -40px;
    }

    .buttons-line-container {
        margin-left: auto;
        margin-right: auto;
    }

    .continue {
        margin-top: 200px;
        font-size: 30px;
    }

    .glitch {
        margin: -110px auto 0 auto;
        font-size: 50px;
        width: 200px;
    }

    .glitch2 {
        font-size: 50px;
        width: 200px;
    }

    /* .glitch:after {
        left: 30px;
    }

    .glitch:before {
        left: 30px;
    } */
    .Home-header {
        text-align: center;
    }

    .input {
        margin: 6px 0px 0px;
    }
    
    .line {
        border: 0;
        height: 0; /* Firefox... */
        box-shadow: 0 0 10px 1px black;
        -moz-box-shadow: 0 0 10px 1px black;
        -webkit-box-shadow: 0 0 10px 1px black;
        border-top: 2px solid blue;
        width: 100%;
        margin-top: -12px;
        margin-left: auto;
        margin-bottom: 12px;
        margin-right: auto;
        z-index: 5555;
        position: relative;
    }

    .line-2{
        border: 0;
        height: 0; /* Firefox... */
        box-shadow: 0 0 10px 1px black;
        -moz-box-shadow: 0 0 10px 1px black;
        -webkit-box-shadow: 0 0 10px 1px black;
        border-top: 2px solid blue;
        width: 60%; 
        margin-bottom: 12px;
        margin-left: auto;
        margin-right: auto;
        z-index: 5555;
        position: relative;
    }

    .music-description {
        margin-top: 20px;
        margin-left: auto;
        margin-right: auto;
        width: 85%;
    }
    
    .home-header-text{
        font-size: 45px;
    }

    .Home-nav-buttons {
        margin-left: auto;
        margin-right: auto;
    }
    
    .Home-nav-buttons:hover {
        color: rgba(255, 0, 0, 0.356);
    }
    
    
    .Home-nav-buttons span {
        align-items: center;
        background: white;
        display: flex;
        height: 100%;
        transition: background .5s ease;
        width: 100%;
    }
    
    .Home-nav-buttons:hover span {
        background: transparent;
    } 

    .rotator {
        padding-right: 0;
        left: 10%;
    }

    .screenText {
        color: rgb(13, 139, 13);
        font-size: 20px;
        margin-left: 0px;
        text-align: center;
        margin-bottom: 11px;
    }

    .submit {
        left: 20%;
        margin-left: -10px;
    }

    @-webkit-keyframes move
    {
        from {
            left: -30%;
        }
        to {
            left: 10%;
        }
    }
    @keyframes move
    {
        from {
            left: -30%;
        }
        to {
            left: 10%;
        }
    }
}