.background-la {
    background-image: url("./images/LA.jpg");
    background-size: cover;
    position: fixed;
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
}

.chase-animation {
    position: absolute;
    -webkit-animation: chase-container 1s;
    animation: chase-container 1s;
    background-color: rgba(255, 255, 255, 0.63);
    padding: 5px;
    border: solid 3px rgb(47, 0, 255);
    border-left: none !important;
}

@-webkit-keyframes chase-container
{
    from {
        left: -100px;
    }
    to {
        left: 0;
    }
}
@keyframes chase-container
{
    from {
        left: -100%;
    }
    to {
        left: 0;
    }
}

.chase-container {
    background-color: white;
    width: 30%;
    position: fixed;
    margin-top: 50px;
    margin-left: 0px;
    
}

.chase-fadout {
    
    background-color: rgba(255, 255, 255, 0.438) !important;
    min-height: 2900px;
    min-width: 100vw;
    font-size: 21px;
    -webkit-animation: fadeout 2s; 
        -moz-animation: fadeout 2s; 
        -ms-animation: fadeout 2s; 
            -o-animation: fadeout 2s; 
            animation: fadeout 2s;
}    
@keyframes fadeout {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-moz-keyframes fadeout {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-webkit-keyframes fadeout {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-ms-keyframes fadeout {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-o-keyframes fadeout {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.fa-close {
    color: red;
    cursor: pointer;
    float: right;
}

.fa-close:hover {
    color: rgba(255, 0, 0, 0.548);
    cursor: pointer;
    float: right;
}