@media (max-width: 1000px) {
    body {
        font-family: 'Varela', sans-serif;
        display: block !important;
        width: 100%;
        position: relative;
    }

    .block-container {
        width: 480px;
        margin-left: auto;
        margin-right: auto;
    }

    .fade-in {
        background-color: white !important;
        
        min-height: 1500px;
        max-width: 1000px !important;
        font-size: 21px;
        margin-left: auto;
        margin-right: auto;
        -webkit-animation: fadein 2s; 
           -moz-animation: fadein 2s; 
            -ms-animation: fadein 2s; 
             -o-animation: fadein 2s; 
                animation: fadein 2s;
    }

    .line {
        border: 0;
        height: 0; /* Firefox... */
        box-shadow: 0 0 10px 1px black;
        -moz-box-shadow: 0 0 10px 1px black;
        -webkit-box-shadow: 0 0 10px 1px black;
        border-top: 2px solid blue;
        width: 600px;
        margin-top: -18px;
        margin-left: -50px;
        margin-bottom: 12px;
        z-index: 5555;
        position: relative;
    }

    .line-2{
        border: 0;
        height: 0; /* Firefox... */
        box-shadow: 0 0 10px 1px black;
        -moz-box-shadow: 0 0 10px 1px black;
        -webkit-box-shadow: 0 0 10px 1px black;
        border-top: 2px solid blue;
        width: 400px; 
        margin: 0px 0px 12px 0px;
        z-index: 5555;
        position: relative;
    }
    .Home-nav-buttons{
        background-image: 
            linear-gradient(
                to bottom, 
                #ffffff 0%, 
                #0f059e5e 100%
                );    
        color: rgb(22, 21, 21);
        cursor: pointer;
        height: 45px;
        width: 75px;
        margin: -30px 0 0 0px;
        border: none;
        font-style: italic;
        font-size: 25px;
        padding: 0 !important;
        z-index: 0;
        position: relative; 
    }
    
    .Home-nav-buttons:hover {
        color: rgba(255, 0, 0, 0.356);
    }
    
    
    .Home-nav-buttons span {
        align-items: center;
        background: white;
        display: flex;
        height: 100%;
        transition: background .5s ease;
        width: 100%;
    }
    
    .Home-nav-buttons:hover span {
        background: transparent;
    } 

    .text-margin {
        max-width: 600px;
    }
  }
