}

.description {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
}

.fade-in {
    background-color: white !important;
    height: 443px;
    /* min-width: 100vw; */
    font-size: 21px;
    margin-left: auto;
    margin-right: auto;
    -webkit-animation: fadein 2s; 
       -moz-animation: fadein 2s; 
        -ms-animation: fadein 2s; 
         -o-animation: fadein 2s; 
            animation: fadein 2s;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-moz-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-ms-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-o-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.Home-container {
    text-align: center;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 50px 50px;
    background-color: white !important;
    max-width: 980px;
}




.img1 {
    float: left;
    position: absolute;
}

.img2 {
    position: absolute;
}

.line {
    border: 0;
    height: 0; /* Firefox... */
    box-shadow: 0 0 10px 1px black;
    -moz-box-shadow: 0 0 10px 1px black;
    -webkit-box-shadow: 0 0 10px 1px black;
    border-top: 2px solid blue;
    max-width: 980px;
    margin-top: -18px;
    margin-left: auto;
    margin-bottom: 7px;
    z-index: 5555;
    position: relative;
    grid-area: header-line;
}

.line-2{
    border: 0;
    height: 0; /* Firefox... */
    box-shadow: 0 0 10px 1px black;
    -moz-box-shadow: 0 0 10px 1px black;
    -webkit-box-shadow: 0 0 10px 1px black;
    border-top: 2px solid blue;
    max-width: 380px; 
    margin-left: auto;
    margin: 0px 0px 12px 600px;
    z-index: 5555;
    position: relative;
    grid-area: buttons;
}

.line-after-description{
    width: 80%;
    border-top: 2px solid black;
}

.line-after-description-2 {
    width: 80%;
    border-top: 2px solid black;
    margin-top: 40px;
}

.margin{
    margin-left: auto;
    margin-right: auto;
}

.middle-text {
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    grid-area: line;
}

.modal {
    border-radius: 5px;
    position: fixed;
    text-align: center;
    font-size: 30px;
    width: 50%;
    height: auto;
    background: rgb(218, 213, 213);
    border: solid 5px blue;
}

.modal-background {
    background-color: rgba(0, 0, 0, 0.938) !important;
    width: 100vw;
    height: 100vh;
    z-index: 999;
    margin-left: auto;
    margin-right: auto;
    position: fixed;
}

.modal-close {
    padding: 7px;
    background-color: red;
    color: white;
    font-size: 20px;
    float: right;
    border-radius: 15px;
    cursor: pointer;
}

.modal-close:hover {
    background-color: rgba(255, 0, 0, 0.472);
}

.music {
    margin-left: 15px;
    font-weight: 600;
    font-size: 25px;
    width: 40px;
}

.music-description{
    margin: -140px 30px 0 240px;
}

.Recruit-Hub-image {
    margin-left: 20px;
}

.ref {
    text-decoration: none !important;
    color: rgb(184, 3, 3);
}

.slide {
    animation: slide 24s ease infinite;
}
  
@keyframes slide {
    0% {
        transform: translateX(0%);
    }

    12.5% {
        transform: translateX(0%);
    }

    50% {
        transform: translateX(-100%);
    }

    62.5% {
        transform: translateX(-100%);
    }

    100% {
        transform: translateX(0);
    }
}
  
.slider1-css {
    max-width: 980px; 
    margin-top: 0px; 
    margin-left: 0px;
}  

.slideshow {
    margin: 0 auto;
    overflow: hidden;
    border: solid 1px white;
}

.slideshow-container {
    max-width: 980px;
    font-size: 0;
    max-height: 300px;
    transition: 1s ease;
}
  

